.Alert{
    position: fixed;
    right: 1%;
    bottom: 2%;
    /*width: 20vw;*/
    padding: 7px 0;
    border-radius: 10px;
    text-align: center;
    background-color: rgba(53, 151, 64, 0.77);
    transition: all 0.3s ease-in-out;
    z-index: 200;
    /* display: flex; */
    display: none;
    width: 150px;
}
.Alert:hover{
    background-color: rgb(53, 151, 64);
}


.Alert p {
    margin: 0;
    color: #fff;
    font-family: 'Roboto', sans-serif;
    font-style: normal;
    font-weight: 500;
    font-size: 1vw;
    /* display: inline-block; */
}


.closealert{
    position: fixed;
    right: 1.4%;
    bottom: 3%;
    font-size: 16px;
    cursor: pointer;
}


.modal-dialog{
    max-width: 50vw !important;
}
.ModalBody{
    display: flex;
    justify-content: space-between;
    width: 100%;
}
.ModalBodyLeftHero, .ModalBodyRightHero{
    width: 45%;
}

.card12 h1{
    font-size: 50px;
}
.input-dollar{
    width: 20%;
}
.InputGroup{
    display: grid;
    grid-template-columns: 20% 20% 20% 20%;
    justify-content: space-between;
}
.input12{
    width: 100%;
    display: block;
}
select{
    background-color: #757575;
    font-size: 20px;
    display: block;
    width: 100%;
    height: 50px;
    padding: 5px 10px;
    border-radius: 10px;
    color: #ffffff;
}
input {
    font-size: 20px;
    display: block;
    width: 100%;
    height: 50px;
    padding: 5px 10px;
    background: none;
    background-image: none;
    border: 1px solid #a0b3b0;
    color: #ffffff;
    background: #757575;
    border-radius: 0;
    border-radius: 10px;
    transition: border-color 0.25s ease, box-shadow 0.25s ease;
    margin-bottom: 20px;
  }
  input:focus {
    outline: 0;
    border-color: #1ab188;
  }

@media screen and (min-width: 571px) and (max-width: 900px) {
    .InputGroup{
        grid-template-columns: 40% 40%;
        justify-content: space-between;
        grid-gap: 50px;
        gap: 50px;
        width: 100%;
        align-items: center;
    }

    #dataDollor{
            width: 40%;
    }
}
@media screen and (max-width: 570px) {
    .InputGroup{
        grid-template-columns: 100%;
        justify-content: space-between;
        grid-gap: 20px;
        gap: 20px;
        width: 100%;
        align-items: center;
    }
    #dataDollor{
        width: 100%;
}
.Dashboard div{
    width: 100%;
}
.modal-dialog{
    max-width: 100vw !important;
}
.ModalBody{
    display: block;
}
.ModalBodyLeftHero, .ModalBodyRightHero{
